import React, {useEffect, useState} from 'react'
import { Link, useNavigate } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCol,
  CContainer,
  CForm,
  CFormCheck,
  CFormInput,
  CImage,
  CFormSelect,
  CInputGroup,
  CInputGroupText,
  CRow,
  CAlert,
  CCardHeader,
  CDatePicker,
  CFormLabel,
  CMultiSelect
} from '@coreui/react-pro'
import CIcon from '@coreui/icons-react'
import { cilLockLocked, cilUser } from '@coreui/icons'
import axios from 'axios'

const AddClient = () => {
  const navigate = useNavigate();
  const [companyName, setCompanyName] = useState("")
  const [clientType, setClientType] = useState("")
  const [logo, setLogo] = useState()
  const [contactId, setContactId] = useState("")
  const [parent, setParent] = useState("")
  const [imageDisplayOrder, setImageDisplayOrder] = useState("")
  const [validationErrors, setValidationErrors] = useState({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [users, setUsers] = useState([])
  const [brands, setBrands] = useState([])
  const [logoPath, setLogoPath] = useState()
  const [brandIds, setBrandIds] = useState([]);
 
  useEffect(() => {
    axios.get('user/findAll', {headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }}).then(response => setUsers(response.data.data))
    .catch(error => console.error('error fetching users', error));

    axios.get('brand/findAll', {headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }}).then(response => {
      let dataFromDatabase = response.data.data;
      const formattedOptions = dataFromDatabase.map(item => ({
        value: item.id,
        label: item.name
      }));
      console.log(formattedOptions);
      setBrands(formattedOptions);  
    })
    .catch(error => console.error('error fetching brand', error));
  }, [])

  const onImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      const objectURL = URL.createObjectURL(file);
      setLogo(file);
      setLogoPath(objectURL);
    }
  };
  

  const changeOrder = (e) => {
    if(document.getElementById('unspecified').checked){
      document.getElementById('imageDisplay1').disabled = true;
      document.getElementById('imageDisplay2').disabled = true;
      document.getElementById('imageDisplay3').disabled = true;
      document.getElementById('imageDisplay4').disabled = true;
      setImageDisplayOrder('unspecified')
    }else{
      document.getElementById('imageDisplay1').disabled = false;
      document.getElementById('imageDisplay2').disabled = false;
      document.getElementById('imageDisplay3').disabled = false;
      document.getElementById('imageDisplay4').disabled = false;
      let value1 = document.getElementById('imageDisplay1').value;
      let value2 = document.getElementById('imageDisplay2').value;
      let value3 = document.getElementById('imageDisplay3').value;
      let value4 = document.getElementById('imageDisplay4').value;
      setImageDisplayOrder(value1+','+value2+','+value3+','+value4);
    }
  }

  const handleChange = (selectedOptions) => {
    const ids = selectedOptions.map(option => option.value);
        console.log(ids);
        setBrandIds(ids);
  };

  const convertFileToBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    });
  };

    const addClientAction = async (e) => {
        e.preventDefault();
        setIsSubmitting(true)
        let image = '';
        if(logo){
           image = await convertFileToBase64(logo);
        }
        console.log(image);
        let payload = {
          companyName: companyName,
          contactId: contactId,
          clientType: clientType,
          imageDisplayOrder: imageDisplayOrder,
          brandIds: brandIds,
          logo: image
        }
        axios.post('/client/create', payload, {headers: { Authorization: `Bearer ${localStorage.getItem('token')}`  }} )
        .then((r) => {
            setIsSubmitting(false)
            navigate("/clients");

        })
        .catch((e) => {
            setIsSubmitting(false)
            if (e.response.data.errors != undefined) {
                setValidationErrors(e.response.data.errors);
            }
        });
    }
  return (
    <div className="bg-body-tertiary min-vh-75 d-flex flex-row align-items-center">
      <CContainer>
              <CForm className="row g-3" onSubmit={(e)=>addClientAction(e)}>
        <CRow className="justify-content-center">
          <CCol md={9} lg={7} xl={12}>
            <CCard className="mb-4">
              <CCardHeader>Company Information</CCardHeader>
              <CCardBody className="p-4">
              <CRow className="mb-3">
                  <CFormLabel htmlFor="companyName" className="col-sm-3 col-form-label">Company Name</CFormLabel>
                  <div className="col-sm-8">
                    <CFormInput type="text" id="companyName" onChange={(e)=>{setCompanyName(e.target.value)}} value={companyName} name='companyName' />
                  </div>
              </CRow>

                <CRow className='mb-4'>
                  <CFormLabel htmlFor="clientType" className="col-sm-3 col-form-label">Client Type</CFormLabel>
                  <div className="col-sm-8">
                    <CFormSelect id="clientType" name='clientType' onChange={(e)=>{setClientType(e.target.value)}} value={clientType} >
                      <option>Choose...</option>
                      <option value='broker' >Broker</option>
                      <option value='distributer' >Distributer</option>
                      <option value='vendor' >Vendor</option>
                      <option value='Manufacturer' >Manufacturer</option>
                    </CFormSelect>
                  </div>
                </CRow>

                <CRow className="mb-3">
                  <CFormLabel htmlFor="Logo" className="col-sm-3 col-form-label">Logo</CFormLabel>
                  <div className="col-sm-8">
                  <CFormInput type="file" id="logo" onChange={onImageChange} name='logo' />
                  <CImage rounded thumbnail src={logoPath} width={200} height={200} />
                  </div>
              </CRow>

              <CRow className="mb-3">
                  <CFormLabel htmlFor="Brands" className="col-sm-3 col-form-label">Brands</CFormLabel>
                  <div className="col-sm-8">
                    <CMultiSelect options={brands} onChange={handleChange} value={brands.filter(option => brandIds.includes(option.value))}/>
                  </div>
              </CRow>

                <CRow className='mb-4'>
                <CFormLabel htmlFor="imageDisplayOrder" className="col-sm-3 col-form-label">Image Display Order</CFormLabel>
                <div className="col-sm-9">
                <CFormCheck id="unspecified" label="Unspecified" className='col-sm-2' onChange={(e)=>{changeOrder(e.target.value)}}/>
                <div className='col-sm-2 float-start ml-5'>
                <CFormSelect id="imageDisplay1" className='col-sm-2 float-start' onChange={(e)=>{changeOrder(e.target.value)}} >
                  <option value='c'>Boxed</option>
                  <option value='o' >Open Boxed</option>
                  <option value='e' >Packaged</option>
                  <option value='i' >Raw</option>
                </CFormSelect></div>
                <div className='col-sm-2 float-start ml-5'>
                <CFormSelect id="imageDisplay2" className='col-sm-2' onChange={(e)=>{changeOrder(e.target.value)}} >
                <option value='c'>Boxed</option>
                  <option value='o' >Open Boxed</option>
                  <option value='e' >Packaged</option>
                  <option value='i' >Raw</option>
                </CFormSelect></div>
                <div className='col-sm-2 float-start ml-5'>
                <CFormSelect id="imageDisplay3" className='col-sm-2' onChange={(e)=>{changeOrder(e.target.value)}} >
                <option value='c'>Boxed</option>
                  <option value='o' >Open Boxed</option>
                  <option value='e' >Packaged</option>
                  <option value='i' >Raw</option>
                </CFormSelect></div>
                <div className='col-sm-2 float-start ml-5'>
                <CFormSelect id="imageDisplay4" className='col-sm-2' onChange={(e)=>{changeOrder(e.target.value)}} >
                <option value='c'>Boxed</option>
                  <option value='o' >Open Boxed</option>
                  <option value='e' >Packaged</option>
                  <option value='i' >Raw</option>
                </CFormSelect></div>
                </div>
                </CRow>

                <CRow className='mb-4'>
                  <CFormLabel htmlFor="contactPerson" className="col-sm-3 col-form-label">Contact Person</CFormLabel>
                  <div className="col-sm-8">
                    <CFormSelect id="contactId" name='contactId' onChange={(e)=>{setContactId(e.target.value)}} value={contactId} >
                      <option>...Choose</option>
                    {users.map((e, key) => {
                        return <option key={key} value={e.id}>{e.username}</option>;
                    })}

                    </CFormSelect>
                  </div>
                </CRow>
                
              </CCardBody>
              </CCard>
              </CCol>
              </CRow>
              <CRow className='p-4'>
              <CCol xs={12}>
                  <CButton color="primary" type="submit" disabled={isSubmitting} className='float-end'>Set Client</CButton>
                </CCol>
              </CRow>
              </CForm>
      </CContainer>
    </div>
  )
}

export default AddClient
